/*-----------------------------------------------
|   Process
-----------------------------------------------*/
.process-item {
  position: relative;
  display: inline-block;
}
.process-item .process-item-number {
  position: absolute;
  color: #e1e1e1;
  left: 0;
  top: 0;
  transform: translate(-127%, -21%);
  font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-weight: 900;
  background-color: white;
  font-size: 3.157rem;
}
@media (min-width: 576px) {
  .process-item .process-item-number {
    font-size: 5.584rem;
  }
}

/*-----------------------------------------------
|   Font family, Position and Overflow [NTR]
-----------------------------------------------*/
.overflow-hidden, .overflow-hidden-x {
  overflow-x: hidden;
}

.overflow-hidden, .overflow-hidden-y {
  overflow-y: hidden;
}

/*-----------------------------------------------
|   Up/Down Indicator
-----------------------------------------------*/
.indicator-arrow {
  display: block;
  position: absolute;
  left: 50%;
  height: 0.625rem;
  width: 0.625rem;
  border-left: 2px solid var(--sparrow-gray-600);
  border-top: 2px solid var(--sparrow-gray-600);
  transform: translateX(-50%) rotate(45deg);
}

.indicator-arrow-one {
  top: 1.375rem;
}

.indicator-arrow-two {
  top: 1.75rem;
}

.indicator {
  position: absolute;
  height: 3rem;
  width: 3rem;
  left: 50%;
}

.indicator-up {
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.indicator-down {
  bottom: 2rem;
  transform: translateX(-50%) rotate(180deg);
}

/*-----------------------------------------------
|   Logos
-----------------------------------------------*/
.logo-sparrow {
  width: 1.875rem;
  height: 1.875rem;
  border-radius: 0.25rem 0 0 0.25rem;
  line-height: 1.875rem;
  text-indent: 0.4375rem;
  text-align: center;
  background-color: #fff;
  color: #000;
  display: inline-block;
  font-size: 3.75rem;
  font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-weight: 900;
  overflow: hidden;
}

.logo {
  height: 2.5rem;
  width: auto;
}

.logo-grid {
  height: auto;
  width: 7rem;
}

.yt-video {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  z-index: -100000;
  pointer-events: none;
}

iframe.yt-video {
  opacity: 0;
}
iframe.yt-video.loaded {
  opacity: 1;
}

/*-----------------------------------------------
|   Isotope
-----------------------------------------------*/
.isotope-nav {
  font-weight: 700;
  border: 1px solid #fff;
  font-size: 0.75rem;
  font-weight: 700;
  color: var(--sparrow-gray-700);
  border-radius: 0.1875rem;
  letter-spacing: 0.02em;
  margin-right: 0.25rem;
  padding: 0.25rem 0.5rem;
}
.isotope-nav.active {
  color: #000 !important;
  border: 1px solid var(--sparrow-black);
}
.isotope-nav:hover {
  color: var(--sparrow-gray-700);
}

.isotope-item {
  visibility: hidden;
}

